  background: transparent;
  padding: 1rem 1rem;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
  font-size: 2rem;
  outline: none;
  box-shadow: 20px 38px 34px -26px rgba(0, 0, 0, 0.2);
  border-radius: 255px 15px 225px 15px/15px 225px 15px 255px;
}

.button {
  -webkit-appearance: none;
  font-family: inherit;
  letter-spacing: inherit;
  cursor: pointer;
  margin-bottom: 1rem;
  color: inherit;

  &:hover {
    box-shadow: 2px 8px 4px -6px rgba(0, 0, 0, 0.3);
  }

  .timer-running & {
    opacity: 0;
    pointer-events: none;
  }
}

.wrap {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 1rem;
  text-align: center;
}

.word {
  margin: 1rem;
  min-width: 74px;
  border: dashed 5px #41403E;

  &.is-selected {
    border-color: #fd6e72;
  }

  .base:not(.timer-running) & {
    cursor: pointer;

    &:hover {
      box-shadow: 2px 8px 4px -6px rgba(0, 0, 0, 0.3);
    }
  }

  .timer-running & {
    &:not(.is-selected) {
      opacity: .6;
    }
  }
}

.word-wrap {
  transition: all .5s ease;

  &:nth-child(2) {
    transition-delay: .1s;
  }

  &:nth-child(3) {
    transition-delay: .2s;
  }

  .base:not(.is-generated) & {
    transform: scale(.2);
    opacity: 0;
  }
}

.wrap-words {
  transition: all .5s ease;

  .base:not(.is-generated) & {
    margin-top: -90px;
  }
}
</style>
