<template>
  <div class="timer">
    <div v-if="seconds > 0" class="seconds">
      {{seconds}}
    </div>
    <div v-else>
      Time’s up!
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      seconds: 60
    }
  },
  mounted() {
    this.interval = setInterval(() => {
      this.seconds--;
      if (this.seconds <= 0) {
        clearInterval(this.interval);

        setTimeout(() => {
          this.$emit('finish');
        }, 3000)
      }
    }, 1000);
  }
}
</script>

<style lang="scss" scoped>
.timer {
  width: 100%;
  text-align: center;
  font-size: 40px;
  font-weight: bold;
  line-height: 80px;
  height: 80px;
  margin: -40px 0;
  position: relative;
}

.seconds {
  position: absolute;
  width: 100px;
  text-align: center;
  left: 0;
  right: 0;
  margin: auto;
  font-size: 80px;
}
</style>
